import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import firebase from '../../firebase.js';

import './product-item.css';


export default class ProductItem extends Component {

  constructor() {
    super();
    this.state = {
      name: '',
      price: '',
      code: '',
      category: '',
      brand: '',
      memory: '',
      pictureUrl: '',
      fav: false,
      justDeleted: false
    };
    this.onFavChange = this.onFavChange.bind(this);
  }

  componentDidMount() {
    const idcode = this.props.codeId;
    const rootRef = firebase.database().ref('/items/');
    const codeRef = rootRef.orderByChild("code").equalTo(`${idcode}`);

    codeRef.on('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
        let item = childSnapshot.val();
        this.setState({
          name: item.name,
          price: item.price,
          code: item.code,
          category: item.category,
          brand: item.brand,
          memory: item.memory,
          pictureUrl: item.pictureUrl
        });
      });
    });

  }

  componentDidUpdate() {
    if (this.props.removeLikeCode) {
      if ((this.props.removeLikeCode === this.state.code)&&(this.state.justDeleted === false)) {
        this.removeLike();
      }
    }
  }

  removeLike() {
    this.setState({
      fav: false,
      justDeleted: true
    })
    let el = document.getElementById(this.state.code).classList;
    if (el.contains("fav-selected")) {
      el.remove("fav-selected");
    } else {
      el.add("fav-selected");
    }
  }

  onFavChange() {
    this.setState({
      fav: !this.state.fav
    });

    if (this.state.fav === false) {
      this.props.onItemAdded(this.state.code, this.state.name, this.state.price, this.state.pictureUrl);
    } else {
      this.props.onDeleted(this.state.code);
    }

    let el = document.getElementById(this.state.code).classList;
    if (el.contains("fav-selected")) {
      el.remove("fav-selected");
    } else {
      el.add("fav-selected");
    }

  }

  render() {

    return (
      <div>
        <div className="card">
          <div className="fav-check" id={this.state.code} onClick={this.onFavChange}></div>
          <Link to={'/item-page/' + this.props.codeId}>
            <div className="img-wrapper">
              <img
                src={this.state.pictureUrl}
                alt="product"/>
             </div>
          </Link>
          <div className="card-body">
            <Link to={'/item-page/' + this.props.codeId}>{this.state.name}</Link>
            <div className="price">{this.state.price} UAH</div>
          </div>
          <div className="card-info">
            <div className="isExist"><img src={require('./img/correct.png')} alt="yes"/><span>In stock</span></div>
            <div className="code">Code: {this.props.codeId} </div>
          </div>
        </div>
      </div>
    )
  }
}
