import React, { Component } from 'react';
//import SearchIcon from "@material-ui/icons/Search";
import SvgIcon from '@material-ui/core/SvgIcon';
import { Link } from 'react-router-dom';
import firebase from '../../firebase.js';

import SearchPanel from '../search-panel';


import './header.css';

function CartIcon(props) {
  return (
    <SvgIcon {...props}>
      <path fill="#ffffff" d="M17,18C15.89,18 15,18.89 15,20A2,2 0 0,0 17,22A2,2 0 0,0 19,20C19,18.89 18.1,18 17,18M1,2V4H3L6.6,11.59L5.24,14.04C5.09,14.32 5,14.65 5,15A2,2 0 0,0 7,17H19V15H7.42A0.25,0.25 0 0,1 7.17,14.75C7.17,14.7 7.18,14.66 7.2,14.63L8.1,13H15.55C16.3,13 16.96,12.58 17.3,11.97L20.88,5.5C20.95,5.34 21,5.17 21,5A1,1 0 0,0 20,4H5.21L4.27,2M7,18C5.89,18 5,18.89 5,20A2,2 0 0,0 7,22A2,2 0 0,0 9,20C9,18.89 8.1,18 7,18Z" />
    </SvgIcon>
  );
}

export default class Header extends Component {

  constructor() {
    super();
    this.state = {
      items: [],
      term: '',
      cartState: false,
      cartData: [],
      cartTotal: 0,
      codeDel: '',
      fromCloseDeleted: '',
      justDeleted: ''
    };
    this.onCartClick = this.onCartClick.bind(this);
  }

  componentDidMount() {
    const rootRef = firebase.database().ref('/items/');
    rootRef.on('value', (snapshot) => {
      let items = snapshot.val();
      let newState = [];
      for (let item in items) {
        newState.push({
          id: item,
          code: items[item].code,
          price: items[item].price,
          name: items[item].name,
          pictureUrl: items[item].pictureUrl
        });
      }
      this.setState({
        items: newState
      });
    });
  }

  maxId = 1;

  createTodoItem(code, name, price, pictureUrl) {
    return {
      code,
      name,
      price,
      pictureUrl,
      quantity: 1,
      id: this.maxId++
    }
  }

  deleteItem = (code) => {
    this.setState(({ cartData }) => {
      const idx = cartData.findIndex((el) =>  el.code === code);

      const before = cartData.slice(0, idx);
      const after = cartData.slice(idx + 1);

      const newArray = [ ...before, ...after];

      return {
        cartData: newArray
      }
    })
    console.warn('item was deleted');
    console.log(code);
    this.setState({
      justDeleted: code,
    })
  };

  addItem = (code, name, price, pictureUrl) => {
    const newItem = this.createTodoItem(code, name, price, pictureUrl);

    this.setState(({ cartData }) => {
        const newArr = [
          ...cartData,
          newItem
        ]

        return {
          cartData: newArr
        };
    });
    console.warn('item was added');
    console.log(code, name, price, pictureUrl);
  };

  deleteItemAndLike(code) {
    this.props.removeLike(code);
    this.deleteItem(code);
    this.setState({
      fromCloseDeleted: code
    })
  }

  totalCount() {
    let totalPrice = 0;
    this.state.cartData.map((item) => {
      let price = item.price;
      price = parseInt(price.replace(/\s+/g, ''), 10);
      totalPrice += price * item.quantity;

      return totalPrice
    })
    this.setState({
      cartTotal: totalPrice
    })
  }

  componentDidUpdate() {
    console.warn('component DID update');
    console.log(this.state.cartData);
    // console.log('this.props.codeFav ' + this.props.codeFav);
    // console.log('this.props.onDeleted1 ' + this.props.onDeleted1);
    // console.log('this.state.fromCloseDeleted ' + this.state.fromCloseDeleted);
    // console.log('this.state.justDeleted ' + this.state.justDeleted);
    // console.log('used code FAv ' + this.state.usedCodeFav);

    const statement1 = this.props.codeFav !== this.props.onDeleted1;
    const statement2 = this.props.codeFav !== this.state.fromCloseDeleted;
    const statement3 = this.props.codeFav === this.state.justDeleted;

    if(statement3 && (this.state.justDeleted !== '')) {
      console.log('need to fix');
    }

    //check whether item like button was clicked twice in a row
    if (statement1&&statement2) {
      //check whether something came on props
      if (this.props.codeFav !== '') {
        //check whether it's the first item
        // console.log(this.state.cartData.length);
        if (this.state.cartData.length !== 0) {
          // console.error('in work');
          //check whether this item already exists
          // console.error(this.state.cartData.some(item => item.code === this.props.codeFav));

          const result = this.state.cartData.some(item => item.code === this.props.codeFav);
          if (result === false) {
            // console.warn('item added from if');
            this.addItem(this.props.codeFav, this.props.nameFav, this.props.priceFav, this.props.pictureUrlFav);
          }
        } else {
          this.addItem(this.props.codeFav, this.props.nameFav, this.props.priceFav, this.props.pictureUrlFav);
          // console.warn('item added from else');
        }
      }
      //
    }

    if (this.props.onDeleted1) {
      if (this.state.codeDel !== this.props.onDeleted1) {
        this.deleteItem(this.props.onDeleted1);
        this.setState({
          codeDel: this.props.onDeleted1
        });
      }
    }
  }

  onCartClick() {
    const newCartState = !this.state.cartState;
    this.setState({ cartState: newCartState });
    this.totalCount();
  }

  onSearchChange = (term) => {
    this.setState({ term });
  };

  search(items, term) {
    if (!term) return [];
    let searched = items.filter((item) => {
      return item.name.toLowerCase().indexOf(term.toLowerCase()) > -1;
    });
    return searched;
  }


  render() {

    console.log('search is here');
    console.log(this.state.term);
    let searchedItems = this.search(this.state.items, this.state.term);
    console.log(searchedItems);

    let elem = document.getElementsByClassName('cart-window');
    if (this.state.cartState === true) {
      for (let i = 0; i < elem.length; i++) {
       elem[i].style.display = "block";
     }
    } else {
      for (let i = 0; i < elem.length; i++) {
       elem[i].style.display = "none";
     }
    }

    let seeTotal = (
      <div className="total">Nothing here :(</div>
    );
    if (this.state.cartData !== 0) {
      seeTotal = (
        <div className="total">
          <span>Total: {this.state.cartTotal} UAH</span>
          <button className="unique-button">Buy</button>
        </div>
      )
    }


    return (
    <div>
      <div className="preheader"></div>
      <header>
        <div className="go2home">
          <Link to="/" className="title-link">I Shop</Link>
        </div>

        <SearchPanel onSearchChange={this.onSearchChange} />
        <ul className="ul-header">
          <li><Link to="/auth" className="title-link">Add Item</Link></li>
          <li><a href="#contacts">About us</a></li>
          <li><a href="#contacts">Contacts</a></li>
        </ul>
        <div className="tocart" onClick={this.onCartClick}>
          <CartIcon />
        </div>
      </header>

      <div className="search-block">
        <div className="inner-search">
          <ul className="search-ul">
            {searchedItems.map((item) => {
              return (
                <li key={item.id} className="search-li">

                  <Link to={'/item-page/' + item.code}>
                    <div className="preview-img">
                      <img
                        src={item.pictureUrl}
                        alt="product"/>
                    </div>
                  </Link>

                  <div className="preview-body">
                    <div className="preview-text">
                      <Link to={'/item-page/' + item.code}>{item.name}</Link>
                    </div>
                    <div className="preview-price">
                      {item.price} UAH
                    </div>
                  </div>

                </li>
              )
            })}
          </ul>
        </div>
      </div>


      <div className="cart-window">
        <span className="close" onClick={this.onCartClick}></span>
        <h5>Your Cart</h5>
        {this.state.cartData.map((item) => {
          return (
            <li key={item.id} className="cart-item">
              <div className="preview-img">
                <img
                  src={item.pictureUrl}
                  alt="product"/>
              </div>
              <div className="preview-body">
                <div className="preview-text">
                  <Link to={'/item-page/' + item.code}>{item.name}</Link>
                </div>
                <div className="preview-price">
                  {item.price} UAH
                </div>
              </div>
              <div className="items-counter">
                <span className="counter-minus"></span>
                <span>{item.quantity}</span>
                <span className="counter-plus"></span>
              </div>
              <span className="close-item" onClick={() => this.deleteItemAndLike(item.code)} ></span>
            </li>
          )
        })}

        {seeTotal}

      </div>
    </div>
    )
  }
}
