import React, { Component } from 'react';
//import { Link } from 'react-router-dom';

import Categories from '../categories';
import ProductItem from '../product-item';
import CarouselSlider from '../carousel';
import Header from '../header';

import './home.css';

export default class Home extends Component {

  constructor() {
    super();
    this.state = {
      active: true,
      nameFav: '',
      codeFav: '',
      priceFav: '',
      pictureUrlFav: '',
      codeDel: '',
      removeLikeCode: ''
    };
    this.addItem = this.addItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.removeLike = this.removeLike.bind(this);
  }

  addItem(code, name, price, pictureUrl) {
    this.setState({
      codeFav: code,
      nameFav: name,
      priceFav: price,
      pictureUrlFav: pictureUrl
    });
  }

  deleteItem(code) {
    this.setState({
      codeDel: code
    });
  }

  removeLike(code) {
   this.setState({
     removeLikeCode: code
   });
  }

  render() {

    return (
      <div className="ishop-app">
        <Header codeFav={this.state.codeFav} nameFav={this.state.nameFav}
                priceFav={this.state.priceFav} pictureUrlFav={this.state.pictureUrlFav}
                onDeleted1={this.state.codeDel}
                removeLike={this.removeLike}
        />
        <div className="row mb2 main-row">
          <div className="col-3">
            <Categories active={this.state.active} />
          </div>
          <div className="col-9">
            <CarouselSlider />
          </div>
        </div>
        <div className="bestsellers"><div className="text">Bestsellers</div><img src={require('../product-item/img/like.png')} alt="like"/></div>
        <div className="leaders-list">
          <ProductItem codeId="20009" onItemAdded={this.addItem} onDeleted={this.deleteItem} removeLikeCode={this.state.removeLikeCode}/>
          <ProductItem codeId="19007" onItemAdded={this.addItem} onDeleted={this.deleteItem} removeLikeCode={this.state.removeLikeCode}/>
          <ProductItem codeId="20012" onItemAdded={this.addItem} onDeleted={this.deleteItem} removeLikeCode={this.state.removeLikeCode}/>
          <ProductItem codeId="20002" onItemAdded={this.addItem} onDeleted={this.deleteItem} removeLikeCode={this.state.removeLikeCode}/>
          <ProductItem codeId="20006" onItemAdded={this.addItem} onDeleted={this.deleteItem} removeLikeCode={this.state.removeLikeCode}/>
        </div>
        <div className="bestsellers"><div className="text">Top Deals</div><img src={require('../product-item/img/like.png')} alt="like"/></div>
        <div className="leaders-list">
          <ProductItem codeId="20010" onItemAdded={this.addItem} onDeleted={this.deleteItem} removeLikeCode={this.state.removeLikeCode}/>
          <ProductItem codeId="19006" onItemAdded={this.addItem} onDeleted={this.deleteItem} removeLikeCode={this.state.removeLikeCode}/>
          <ProductItem codeId="20003" onItemAdded={this.addItem} onDeleted={this.deleteItem} removeLikeCode={this.state.removeLikeCode}/>
          <ProductItem codeId="20005" onItemAdded={this.addItem} onDeleted={this.deleteItem} removeLikeCode={this.state.removeLikeCode}/>
          <ProductItem codeId="19002" onItemAdded={this.addItem} onDeleted={this.deleteItem} removeLikeCode={this.state.removeLikeCode}/>
        </div>
      </div>
    );
  }
}
