import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import firebase from '../../firebase.js';

import './add-item.css';

export default class AddItem extends Component {

  constructor() {
    super();
    this.state = {
      name: '',
      price: '',
      code: '',
      category: '',
      brand: '',
      memory: '',
      pictureUrl: '',
      items: []
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(e) {
    this.setState({
      [e.target.name] : e.target.value
    });
  }

  handleSubmit(e) {
    e.preventDefault();
    const itemsRef = firebase.database().ref('items');
    const item = {
      name: this.state.name,
      price: this.state.price,
      code: this.state.code,
      category: this.state.category,
      brand: this.state.brand,
      memory: this.state.memory,
      pictureUrl: this.state.pictureUrl
    }
    itemsRef.push(item);
    this.setState({
      name: '',
      price: '',
      code: '',
      category: '',
      brand: '',
      memory: '',
      pictureUrl: ''
    });
    alert('New item with this parameters: name: ' + this.state.name + ' /price: ' + this.state.price + ' /id: ' + this.state.code + ' /category: ' + this.state.category + ' /brand: ' + this.state.brand + ' /memory type: ' + this.state.memory + '  successfully added');
  }
  componentDidMount() {
    const itemsRef = firebase.database().ref('items');
    itemsRef.on('value', (snapshot) => {
      let items = snapshot.val();
      let newState = [];
      for (let item in items) {
        newState.push({
          id: item,
          name: items[item].name,
          price: items[item].price,
          code: items[item].code,
          category: items[item].category,
          brand: items[item].brand,
          memory: items[item].memory,
          pictureUrl: items[item].pictureUrl
        });
      }
      this.setState({
        items: newState
      });
    });
  }
  removeItem(itemId) {
    const itemRef = firebase.database().ref(`/items/${itemId}`);
    itemRef.remove();
  }



  render() {
    return (
      <div>
        <Link to="/">Home</Link>
        <form className="add-item" onSubmit={this.handleSubmit}>
          <span>Name of product</span>
          <input type="text" placeholder="name of product" name="name" required onChange={this.handleChange} value={this.state.name}/>
          <span>Price of product</span>
          <input type="text" placeholder="price" name="price" required onChange={this.handleChange} value={this.state.price} />
          <span>Product id</span>
          <input type="text" placeholder="code" name="code" required onChange={this.handleChange} value={this.state.code} />
          <span>Pick a category of item</span>
          <input type="text" placeholder="category" name="category" required onChange={this.handleChange} value={this.state.category} />
          <span>Product brand</span>
          <input type="text" placeholder="brand" name="brand" required onChange={this.handleChange} value={this.state.brand} />
          <span>What about memory</span>
          <input type="text" placeholder="memory type" name="memory" required onChange={this.handleChange} value={this.state.memory} />
          <span>Picture url</span>
          <input type="text" placeholder="enter url of picture of item" name="pictureUrl" required onChange={this.handleChange} value={this.state.pictureUrl} />
          <button>Add Product</button>
        </form>

        <section className='display-item'>
             <div className="wrapper">
               <ul>
                 {this.state.items.map((item) => {
                   return (
                     <li key={item.id} className="display-item-card">
                       <div className="card">
                         <Link to={'/item-page/' + item.code}>
                           <div className="img-wrapper">
                             <img
                               src={item.pictureUrl}
                               alt="product"/>
                           </div>
                         </Link>
                         <div className="card-body">
                           <Link to={'/item-page/' + item.code}>{item.name}</Link>
                           <div className="price">{item.price} UAH</div>
                         </div>
                         <div className="card-info">
                           <div className="isExist"><button onClick={() => this.removeItem(item.id)} className="unique-button">Remove Item</button></div>
                           <div className="code">Code: {item.code}</div>
                         </div>
                       </div>
                     </li>
                   )
                 })}
               </ul>
             </div>
         </section>

      </div>
    );
  }
}
