import React, { Component } from 'react';
import Paper from '@material-ui/core/Paper';

import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';

import './carousel.css';

export default class CarouselSlider extends Component {

  render() {

    return (
      <Paper className="carousel" elevation={2}>
      <Carousel
        showThumbs={false}
        infiniteLoop={true}
        useKeyboardArrows={true}
        autoPlay={true}
        interval={5000}
        transitionTime={500}
        emulateTouch={true}
        showStatus={false}
      >
        <div>
          <img src="https://www.ahstatic.com/photos/6157_de_01_p_2048x1536.jpg" alt="slider"/>
        </div>
        <div>
          <img src="https://www.ahstatic.com/photos/6157_de_01_p_2048x1536.jpg" alt="slider"/>
        </div>
        <div>
          <img src="https://www.ahstatic.com/photos/6157_de_01_p_2048x1536.jpg" alt="slider"/>
        </div>
        <div>
          <img src="https://www.ahstatic.com/photos/6157_de_01_p_2048x1536.jpg" alt="slider"/>
        </div>
        <div>
          <img src="https://www.ahstatic.com/photos/6157_de_01_p_2048x1536.jpg" alt="slider"/>
        </div>
      </Carousel>
      </Paper>
    )
  }
}
