import React, {Component} from 'react';

import Header from '../header';
import Categories from '../categories';
import Sidebar from '../sidebar';
import ProductItem from '../product-item';
import firebase from '../../firebase.js';
//import { Link } from 'react-router-dom';

import './shop-page.css';

export default class ShopPage extends Component{

  constructor() {
    super();
    this.state = {
      code: '',
      price: '',
      brand: '',
      memory: '',
      items: [],
      maxPrice: 0,
      checkBoxes: {
        apple: false,
        samsung: false,
        huawei: false,
        oneplus: false,
        xiaomi: false,
        memory1: false,
        memory2: false,
        memory3: false
      },
      value: {
        min: 0,
        max: 1
      },
      filterBrand: '',
      filterMemory: '',
      nameFav: '',
      codeFav: '',
      priceFav: '',
      pictureUrlFav: '',
      codeDel: '',
      removeLikeCode: ''
    };
    this.addItem = this.addItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.removeLike = this.removeLike.bind(this);
  }

  componentDidMount() {
    let categoryId = this.props.match.params.category_id;
    console.log('categoryId is in didmount ' + categoryId);
    const rootRef = firebase.database().ref('/items/');
    const itemsRef = rootRef.orderByChild("category").equalTo(`${categoryId}`);

    itemsRef.on('value', (snapshot) => {
      let items = snapshot.val();
      let newState = [];
      for (let item in items) {
        newState.push({
          id: item,
          code: items[item].code,
          price: items[item].price,
          brand: items[item].brand,
          memory: items[item].memory
        });
      }
      this.setState({
        items: newState
      });
      const itemsList = newState;

      console.warn('here would be items from didmount');
      console.log(itemsList);


      let maxPrice = 0;
      for (let i = 0; i < itemsList.length; i++) {
        let x = itemsList[i].price;
        x = parseInt(x.replace(/\s+/g, ''), 10);
        if (x > maxPrice) {
          maxPrice = x;
        }
      }
      this.setState({
        maxPrice: maxPrice
      });

    });
  }


  updateData = (value) => {
     this.setState({
       checkBoxes: value
     })
     const checkBoxx = value;
     this.onFilterChange(checkBoxx);
  }

  updateRange = (value) => {
     this.setState({
       value: value
     })
  }

  onFilterChange(value) {
    let box = Object.entries(value);
    let filterBrand = "";
    let filterMemory = "";
    for (let i = 0; i < box.length; i++) {
      let innerBox = box[i];

      if (innerBox[1] === true ) {
        if ((innerBox[0] === 'memory1')||(innerBox[0] === 'memory2')||(innerBox[0] === 'memory3')) {
          filterMemory = innerBox[0];
        } else {
          filterBrand = innerBox[0];
        }
      }
    }
    this.setState({
      filterBrand: filterBrand,
      filterMemory: filterMemory
    })
  }

  filter(items, filterBrand, filterMemory)  {

    console.log(filterMemory);

    let min = this.state.value.min;
    let max = this.state.value.max;
    if ((max !== 1)||(filterBrand !== '')||(filterMemory !== '')) {
      // eslint-disable-next-line
      return items.filter((item) => {
        let price = item.price;
        price = parseInt(price.replace(/\s+/g, ''), 10);
        if ((max === 1)&&(item.brand === filterBrand)&&(filterMemory === '')) {
          return item;
        } else if ((price >= min)&&(price <= max)&&(filterBrand === '')&&(filterMemory === '')) {
          return item;
        } else if (((price >= min)&&(price <= max))&&(item.brand === filterBrand)&&(filterMemory === '')) {
          return item;
        } else if ((max === 1)&&(filterBrand === '')&&(item.memory === filterMemory)) {
          return item;
        } else if ((max === 1)&&(item.brand === filterBrand)&&(item.memory === filterMemory)) {
          return item;
        } else if ((price >= min)&&(price <= max)&&(filterBrand === '')&&(item.memory === filterMemory)) {
          return item;
        } else if (((price >= min)&&(price <= max))&&(item.brand === filterBrand)&&(item.memory === filterMemory)) {
          return item;
        }
      });
    } else {
      return items;
    }

  }

  addItem(code, name, price, pictureUrl) {
    this.setState({
      codeFav: code,
      nameFav: name,
      priceFav: price,
      pictureUrlFav: pictureUrl
    });
  }

  deleteItem(code) {
    this.setState({
      codeDel: code
    });
  }

  removeLike(code) {
   this.setState({
     removeLikeCode: code
   });
  }

  render() {

    let range = (
      <span></span>
    );
    if (this.state.value.max !== 1) {
      range = <span>/ Price range is from {this.state.value.min} to {this.state.value.max}</span>
    }
    let selectedBrand = (
      <span></span>
    );
    if (this.state.filterBrand !== '') {
      selectedBrand = <span>/ Filtered by brand {this.state.filterBrand}</span>
    }

    let selectedMemory= (
        <span></span>
    );
    if (this.state.filterMemory !== '') {
      let result = '';
      // eslint-disable-next-line
      switch (this.state.filterMemory) {
        case 'memory1':
          result = '3/32 Gb';
          break;
        case 'memory2':
          result = '4/64 Gb';
          break
        case 'memory3':
          result = '6/128 Gb';
          break;
      }
      selectedMemory = <span>/ Filtered by memory {result}</span>
    }

    const visibleItems = this.filter(this.state.items, this.state.filterBrand, this.state.filterMemory);

    console.log('maxPrice = ' + this.state.maxPrice);

    return (
      <div className="shop-page">
        <Header codeFav={this.state.codeFav} nameFav={this.state.nameFav}
                priceFav={this.state.priceFav} pictureUrlFav={this.state.pictureUrlFav}
                onDeleted1={this.state.codeDel}
                removeLike={this.removeLike}
        />

        <div className="row mb2 main-row">
          <div className="col-3">
            <Categories />
            <Sidebar maxPrice={this.state.maxPrice} updateData={this.updateData} updateRange={this.updateRange}/>
          </div>
          <div className="col-9">
            <div className="selected"><span>Selected items</span>{range}{selectedBrand}{selectedMemory}</div>
            <div className="leaders-list-2">

            {visibleItems.slice(0).reverse().map((item) => {
              return (
                <div key={item.id} className="shop-items">
                  <ProductItem codeId={item.code}
                              onItemAdded={this.addItem} 
                              onDeleted={this.deleteItem}
                              removeLikeCode={this.state.removeLikeCode}
                  />
                </div>
              )
            })}

            </div>
          </div>
        </div>
      </div>
    );
  }
}
