import React from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { Link } from 'react-router-dom';

import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import './categories.css';

const styles = theme => ({
  root: {
    width: '100%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '100%',
  },
});


function ListItemLink2(props) {
  const { primary, to, num } = props;
  let bgi;
  switch (num) {
    case '1':
      bgi = { backgroundImage: `url('https://i.citrus.ua/uploads/menu-icons/c_phone.svg')` };
      break;
    case '2':
      bgi = { backgroundImage: `url('https://i.citrus.ua/uploads/menu-icons/c_nout_pc.svg')` };
      break;
    case '3':
      bgi = { backgroundImage: `url('https://i.citrus.ua/uploads/menu-icons/%D1%81_smartwatch.svg')` };
      break;
    case '4':
      bgi = { backgroundImage: `url('https://i.citrus.ua/uploads/menu-icons/c_acoustic.svg')` };
      break;
    case '5':
      bgi = { backgroundImage: `url('https://i.citrus.ua/uploads/menu-icons/%D1%81_health_sport.svg')` };
      break;
    case '6':
      bgi = { backgroundImage: `url('https://i.citrus.ua/uploads/menu-icons/c_tv.svg')` };
      break;
    case '7':
      bgi = { backgroundImage: `url('https://i.citrus.ua/uploads/menu-icons/c_accessories.svg')` };
      break;
    default:
      bgi = { backgroundImage: `url('https://i.citrus.ua/uploads/menu-icons/c_games.svg')` };
  }

  return (
    <li>
      <ListItem button component={Link} to={to}>
        <span className="cat-icon" style={bgi}></span>
        <ListItemText primary={primary} />
      </ListItem>
    </li>
  );
}

ListItemLink2.propTypes = {
  primary: PropTypes.node.isRequired,
  to: PropTypes.string.isRequired,
};

class Categories extends React.Component {
  state = {
    expanded: null,
  };

  handleChange = panel => (event, expanded) => {
    this.setState({
      expanded: expanded ? panel : false,
    });
  };

  render() {
    const { classes } = this.props;
    //const { expanded } = this.state;

    return (
      <div className="cat-block">
        <div className={classes.root}>
          <ExpansionPanel defaultExpanded={this.props.active} className="cat-block">
            <ExpansionPanelSummary expandIcon={<ExpandMoreIcon /> } className="expand-cat">
              <h6>Categories</h6>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails className="categories">
                <List component="nav" className="categories-sidebar">
                  <ListItemLink2 to="/shop-page/smartphones" primary="Smartphones" num="1"/>
                  <ListItemLink2 to="/shop-page/notebooks" primary="Notebooks" num="2" />
                  <ListItemLink2 to="/shop-page" primary="Smart Watches" num="3" />
                  <ListItemLink2 to="/shop-page" primary="Audio" num="4" />
                  <ListItemLink2 to="/shop-page" primary="Fitness and health" num="5" />
                  <ListItemLink2 to="/shop-page" primary="TV" num="6" />
                  <ListItemLink2 to="/shop-page" primary="Accessories" num="7" />
                </List>
            </ExpansionPanelDetails>
          </ExpansionPanel>
        </div>
      </div>
    );
  }
}

Categories.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Categories);
