import React, {Component} from 'react';
//import Paper from '@material-ui/core/Paper';
import { Link } from 'react-router-dom';
import firebase from '../../firebase.js';

import Header from '../header';
//import Categories from '../categories';

import './item-page.css';


export default class ItemPage extends Component {

  constructor() {
    super();
    this.state = {
      name: '',
      price: '',
      code: '',
      category: '',
      brand: '',
      memory: '',
      pictureUrl: ''
    };
  }

  componentDidMount() {
    const itemId = this.props.match.params.item_id;
    console.log('itemId is in didmount ' + itemId);
    const rootRef = firebase.database().ref('/items/');
    const codeRef = rootRef.orderByChild("code").equalTo(`${itemId}`);

    codeRef.on('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
        let item = childSnapshot.val();
        this.setState({
          name: item.name,
          price: item.price,
          code: item.code,
          category: item.category,
          brand: item.brand,
          memory: item.memory,
          pictureUrl: item.pictureUrl
        });
      });
    });

  }



  render() {

    return (
      <div>
        <Header />
        <div className="item-wrapper">
          <div className="inner-wrapper">
            <div className="selected-cat">
              <ul className="breadcrumb">
                <li><Link to="/" className="title-link">Home</Link></li>
                <li><Link to={'/shop-page/' + this.state.category} className="title-link">{this.state.category}</Link></li>
                <li><Link to="#" className="title-link">{this.state.brand}</Link></li>
              </ul>
              <div className="item-code">Code: {this.state.code}</div>
            </div>
            <div className="row mb2">
              <div className="col-6">
                <div className="img-item-wrapper">
                  <img
                    src={this.state.pictureUrl}
                    alt="product"/>
                </div>
              </div>
              <div className="col-6">
                <div className="main-info">
                  <h2>{this.state.name}</h2>
                  <div className="price-product">{this.state.price} UAH</div>
                </div>
                <div className="divider"></div>
                <form className="forms" action="post">
                  <span>Name</span>
                  <input type="text" name="name" required placeholder="Enter Your name" />
                  <span>Phone number</span>
                  <input type="text" name="phone" required placeholder="+38(0xx)xxxx-xxx" />
                  <div className="order-block">
                    <button className="btn">Order Now</button>
                    <div className="isExist"><img src={require('../product-item/img/correct.png')} alt="yes"/><span>In stock</span></div>
                  </div>
                </form>
              </div>
            </div>

          </div>
        </div>
      </div>
    );
  }
}
