import React, {Component} from 'react';

import InputRange from 'react-input-range';
import Checkbox from '@material-ui/core/Checkbox';
//import FormLabel from '@material-ui/core/FormLabel';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
//import firebase from '../../firebase.js';

import './sidebar.css';
import 'react-input-range/lib/css/index.css';

export default class Sidebar extends Component{

  constructor(props) {
    super(props);

    this.state = {
      value: {
        min: 0,
        max: 30000
      },
      checkBoxes: {
        apple: false,
        samsung: false,
        huawei: false,
        oneplus: false,
        xiaomi: false,
        memory1: false,
        memory2: false,
        memory3: false
      },
      calledX: false,
      filters: []
    };
    this.onChangeInput = this.onChangeInput.bind(this);
    this.onChangeRange = this.onChangeRange.bind(this);
  }

  componentDidMount() {

    const filters = [
      {
        title: 'Brand',
        content: [
          {label: 'Apple', value: 'apple'},
          {label: 'Samsung', value: 'samsung'},
          {label: 'Huawei', value: 'huawei'},
          {label: 'OnePlus', value: 'oneplus'},
          {label: 'Xiaomi', value: 'xiaomi'}
        ]
      },
      {
        title: 'Memory',
        content: [
          {label: '3/32 Gb', value: 'memory1'},
          {label: '4/64 Gb', value: 'memory2'},
          {label: '6/128 Gb', value: 'memory3'}
        ]
      }
    ]

    this.setState({
      filters: filters
    });
  }

  componentDidUpdate() {
    if ((this.state.value.max !== this.props.maxPrice) && (this.state.calledX !== true)) {
      this.setState({
        value: {
          min: 0,
          max: this.props.maxPrice
        }
      });
    }
  }

  maxxx() {
    this.setState({
      calledX: true
    });
  }


  onChangeInput(e) {
    this.maxxx();
    this.setState({
      value: {
        ...this.state.value,
        [e.target.name] : e.target.value
      }
    });
  }

  onChangeRange(value) {
    this.maxxx();
    this.setState({ value });
    this.props.updateRange(this.state.value);
  }

  checkboxChange = name => event => {
    const checkUpd = {
      ...this.state.checkBoxes,
      [name]: event.target.checked
    }

    this.setState({
      checkBoxes: {
        ...this.state.checkBoxes,
        [name]: event.target.checked
      }
    });
    this.props.updateData(checkUpd);
  };


  render() {

    const { apple, filters } = this.state;

    return (
      <div className="sidebar">
        <div className="side-box">
          <p>Price </p>
          <div className="roww">
            <div className="half">
              <span>from</span>
              <div className="el-input">
                <input autoComplete="off" type="number" className="el-input-inner" name="min" onChange={this.onChangeInput} value={this.state.value.min} />
              </div>
            </div>
            <div className="half">
              <span>to</span>
              <div className="el-input">
                <input autoComplete="off" type="number" className="el-input-inner" name="max" onChange={this.onChangeInput} value={this.state.value.max}/>
              </div>
            </div>
          </div>
          <button onClick={() => { this.props.updateRange(this.state.value)}} className="unique-button">Sort by price</button>

          <form className="form">
            <InputRange
              draggableTrack
              minValue={0}
              maxValue={this.props.maxPrice}
              onChange={this.onChangeRange}
              value={this.state.value}
            />
          </form>

        </div>
        {filters.map((item, index) => (
          <div key={index} className="side-box">
            <p>{item.title}</p>
            <FormGroup>
            {item.content.map((c, i) => (
              <div key={i}>
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={apple}
                      onChange={this.checkboxChange(`${c.value}`)}
                      value={c.value}
                      color="primary"
                    />
                  }
                  label={c.label}
                  className="checkbox-item"
                />

              </div>
            ))}
            </FormGroup>
          </div>
        ))}
      </div>
    );
  }
}
