import React, { Component } from 'react';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';

//import ProductItem from '../product-item';
import ShopPage from '../shop-page';
import Home from '../home';
import ItemPage from '../item-page';
import AddItem from '../add-item';
import Auth from '../auth';


import './app.css';

export default class App extends Component {

  render() {

    return (
      <div>
      <Router>
          <div>

            <Switch>
              <Route path="/" exact component={Home} />
              <Route path="/shop-page" exact component={ShopPage} />
              <Route path="/shop-page/:category_id" render={(props) => <ShopPage key={props.match.params.category_id} {...props} />} />
              <Route path="/item-page/:item_id" exact component={ItemPage} />
              <Route path="/add-item" exact component={AddItem} />
              <Route exact path="/auth" component={Auth}/>

              <Route render={() => <h2>Page not found</h2>} />
            </Switch>

          </div>
        </Router>

      </div>

    );
  }
}
